import React from 'react';

import { screen } from '../constants';

const noop = () => {};

class HeaderNav extends React.Component {
  static defaultProps = {
    onMouseOver: noop,
    onMouseOut: noop,
  };

  state = {
    movileView: false,
    expanded: false,
  };

  componentDidMount() {
    this.setState({ movileView: window.innerWidth < screen.tabletMax });

    window.addEventListener('resize', this.onResizeWindow);
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.onResizeWindow);
  }

  onResizeWindow = () => {
    const nextState = { movileView: window.innerWidth < screen.tabletMax };

    if (this.state.expanded && this.state.movileView && !nextState.movileView) {
      nextState.expanded = false;
    }

    this.setState(nextState);
  };

  onMouseOver = () => {
    if (!this.props.showNavigationDrawer) {
      this.setState({ expanded: true }, this.props.onMouseOver);
    }
  };

  onMouseOut = () => {
    if (!this.props.showNavigationDrawer) {
      this.setState({ expanded: false }, this.props.onMouseOut);
    }
  };

  onClick = () => {
    if (this.props.showNavigationDrawer) {
      this.setState({ expanded: !this.state.expanded });
    }
  };

  render() {
    const { children = '?', content, showNavigationDrawer } = this.props;
    const { expanded } = this.state;

    return (
      <div
        className="header-nav"
        onClick={this.onClick}
        onMouseOver={this.onMouseOver}
        onMouseOut={this.onMouseOut}
      >
        <div className="header-nav__trigger">{children}</div>
        {content && (
          <div
            className={`header-nav__content ${
              showNavigationDrawer ? '' : 'header-nav__content_absolute'
            }`}
            style={{
              ...(expanded && {
                padding: '0 20px 16px',
                maxHeight: '100vh',
                visibility: 'visible',
                opacity: 1,
                marginTop: 14,
              }),
              ...(showNavigationDrawer
                ? {
                    overflow: 'hidden',
                  }
                : {
                    boxShadow: '0 6px 20px 0 rgba(23, 28, 33, 0.16)',
                  }),
            }}
          >
            <div className="header-nav__arrow" />
            {content}
          </div>
        )}
        <style jsx>{`
          .header-nav {
            position: relative;
            display: inline-block;
            padding: 10px 0;
            z-index: 10;
          }

          .header-nav__content {
            min-width: 200px;
            max-width: 260px;
            width: max-content;
            max-height: 0;

            border-radius: 3px;
            background-color: #fff;

            visibility: hidden;
            opacity: 0;

            transition: 0.33s;
          }

          .header-nav__content_absolute {
            position: absolute;
            margin-top: 28px;
            top: 100%;
            left: 50%;
            transform: translateX(-50%);
          }

          .header-nav__trigger {
            user-select: none;
          }

          .header-nav__arrow {
            position: absolute;
            bottom: 100%;
            left: 50%;

            width: 50px;
            height: 25px;
            overflow: hidden;

            transform: translateX(-50%);
          }

          .header-nav__arrow::after {
            content: '';
            position: absolute;

            bottom: 0;
            left: 50%;

            width: 15px;
            height: 15px;

            background-color: #fff;
            box-shadow: 0 6px 20px 0 rgba(23, 28, 33, 0.16);

            transform: translateX(-50%) translateY(50%) rotate(45deg);
          }
        `}</style>
        <style jsx global>{`
          .header-nav__popup:hover .header-nav__item {
            color: #8f99ae;
          }

          .header-nav__item {
            color: #1f325d;
          }

          .header-nav__item:hover {
            color: #1f325d !important;
            cursor: pointer;
          }
        `}</style>
      </div>
    );
  }
}

export default HeaderNav;
