import React from 'react';
import { Link } from 'gatsby';

import Container from './container';
import RightArrowIcon from './icons/RightArrow';
import ResolveLogo from './icons/ResolveLogo';

import { fonts, screen, colors } from '../constants';

export default ({ title, buttonText, items }) => {
  function onClickGetDemo() {}
  const darkSkin = true;
  const year = new Date().getFullYear();

  return (
    <footer className="block-footer">
      <Container>
        <div className="block-footer__inner">
          <div className="block-footer__get-started">
            {title}{' '}
            {buttonText && (
              <span
                className="block-footer__get-started-link"
                onClick={onClickGetDemo}
              >
                {buttonText}{' '}
                <RightArrowIcon
                  color={
                    darkSkin ? colors.footerDarkLink : colors.footerLightLink
                  }
                />
              </span>
            )}
          </div>

          <hr />

          <div className="block-footer__row_links">
            <div className="block-footer__icons">
              <ResolveLogo skin="baliHai" />
              <div className="block-footer__corp">©{year} Resolve Corp.</div>
            </div>
            <div className="block-footer__links">
              {items.map(item => (
                <div key={item.id} className="block-footer__link-group">
                  <div className="block-footer__link-group-title">
                    {item.title}
                  </div>
                  {item.links.map(link => (
                    <Link
                      key={link.id}
                      className="block-footer__link"
                      to={link.link}
                    >
                      {link.title}
                    </Link>
                  ))}
                </div>
              ))}
            </div>
          </div>
        </div>
      </Container>
      <style jsx>
        {`
          .block-footer {
            background: linear-gradient(
              -12deg,
              ${darkSkin
                  ? colors.footerDarkBackground
                  : colors.footerLightBackground}
                63%,
              ${colors.pageBackground} 63.1%
            );
            min-height: 770px;
            padding-bottom: 104px;
          }
          .block-footer__inner {
            padding-top: 510px;
          }

          .block-footer__get-started {
            font-family: ${fonts.gilroy};
            font-size: 24px;
            font-weight: 600;
            line-height: 1.33;
            letter-spacing: 0px;
            color: ${darkSkin ? colors.footerDark : colors.footerLight};
            font-weight: 600;
            text-align: center;
          }
          .block-footer__get-started-link {
            color: ${darkSkin ? colors.footerDarkLink : colors.footerLightLink};
            cursor: pointer;
            display: inline-flex;
            align-items: center;
            margin-left: 10px;
          }

          .block-footer__row_links {
            display: flex;
            justify-content: space-between;
          }

          .block-footer__links {
            display: flex;
            flex-direction: row;
            text-align: right;
          }
          .block-footer__link-group {
            display: flex;
            flex-direction: column;
            margin-left: 45px;
          }
          .block-footer__link-group-title {
            font-size: 16px;
            font-weight: 500;
            line-height: 1.13;
            letter-spacing: 1.3px;
            text-align: right;
            color: ${darkSkin
              ? colors.footerDarkGroupTitle
              : colors.footerLightGroupTitle};
            text-transform: uppercase;
            margin-bottom: 25px;
          }

          .block-footer__icons {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
          }
          .block-footer__corp {
            font-size: 18px;
            line-height: 1;
            letter-spacing: 0.6px;
            color: ${darkSkin ? colors.footerDarkCorp : colors.footerLightCorp};
          }
          hr {
            margin: 50px 0;
            opacity: 0.15;
            background-color: ${darkSkin
              ? colors.footerDarkSeparator
              : colors.footerLightSeparator};
          }
          .block-footer__get-started_links {
            margin-top: 75px;
            display: flex;
            justify-content: flex-end;
          }
          .block-footer__get-started_link:first-child {
            margin-left: 0;
          }

          @media (max-width: ${screen.tabletMax}px) {
            .block-footer {
              background: linear-gradient(
                -14deg,
                ${darkSkin
                    ? colors.footerDarkBackground
                    : colors.footerLightBackground}
                  75%,
                ${colors.pageBackground} 75.1%
              );
            }
            .block-footer__inner {
              padding-top: 300px;
            }

            .block-footer__row_links {
              flex-direction: column;
            }

            .block-footer__icons {
              flex-direction: row;
              align-items: center;
              margin-bottom: 50px;
            }

            .block-footer__links {
              justify-content: space-between;
            }
          }

          @media (max-width: ${screen.mobileMax}px) {
            .block-footer__get-started {
              text-align: left;
            }
            .block-footer__link-group {
              width: 49%;
              margin: 0 0 50px 0;
            }
            .block-footer__links {
              flex-wrap: wrap;
            }
          }
        `}
      </style>
      <style jsx global>{`
        .block-footer__link {
          font-family: ${fonts.halyardDisplay};
          font-size: 18px;
          line-height: 1;
          letter-spacing: 0.6px;
          text-align: right;
          font-weight: 300 !important;
          color: #576686 !important;
          margin-bottom: 25px;
        }
        .block-footer__link:hover {
          color: #8f99ae !important;
        }
        .block-footer__link-group > a:last-child {
          margin-bottom: 0;
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-footer__link {
            margin-bottom: 18px;
            font-size: 16px;
          }
        }

        @media (max-width: ${screen.mobileMax}px) {
          .block-footer__links > a:last-child {
            margin-bottom: 0;
          }
        }
      `}</style>
    </footer>
  );
};
